import './TeamCard.css';
import React, { useState } from 'react'
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import Button from '@material-ui/core/Button';

const TeamCard = (props) => {
  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <div className="card" onClick={handleClickOpen}>
        <img className="image" src={props.img} alt=""></img>
        <div className="name">{props.name.toUpperCase()}</div>
        <div className="title">{props.title}</div>
        <div className="story">View My Story</div>
      </div>
      <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open}>
        <MuiDialogTitle onClose={handleClose} className="dialog-title">
          {props.name}
        </MuiDialogTitle>
        <MuiDialogContent dividers>
          {props.content}
        </MuiDialogContent>
        <MuiDialogActions>
          <Button autoFocus onClick={handleClose} color="primary">
            Close
          </Button>
        </MuiDialogActions>
      </Dialog>
    </div>
  );
}

export default TeamCard;
